:root {
    --marquee-width: 80vw;
    --marquee-height: 12vh;
    /* --marquee-elements: 12; */ /* defined with JavaScript */
    --marquee-elements-displayed: 5;
    --marquee-element-width: calc(var(--marquee-width) / var(--marquee-elements-displayed));
    --marquee-animation-duration: calc(var(--marquee-elements) * 3s);
}

.carousel-title{
    text-align: center;
    color: white;
} 

.light {
    color: black;
}

.carousel-box{
    margin: 180px;
    display: flex;
    align-items: center;
}

.marquee {
    width: 80vw;
    width: var(--marquee-width);
    height: 12vh;
    height: var(--marquee-height);
    background-color: #111;
    color: #eee;
    overflow: hidden;
    position: relative;
    
}
.marquee:before,
.marquee:after {
    position: absolute;
    top: 0;
    width: 10rem;
    height: 100%;
    content: "";
    z-index: 1;
}
.marquee:before {
    left: 0;
    background: linear-gradient(to right, #111 0%, transparent 100%);
}
.marquee:after {
    right: 0;
    background: linear-gradient(to left, #111 0%, transparent 100%);
}
.marquee-content {
    list-style: none;
    height: 100%;
    display: flex;
    -webkit-animation: scrolling calc(var(--marquee-elements) * 3s) linear infinite both;
            animation: scrolling calc(var(--marquee-elements) * 3s) linear infinite both;
    -webkit-animation: scrolling var(--marquee-animation-duration) linear infinite both;
            animation: scrolling var(--marquee-animation-duration) linear infinite both;
}
/* .marquee-content:hover {
  animation-play-state: paused;
} */
@-webkit-keyframes scrolling {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
                transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
        -webkit-transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
                transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
    }
}
@keyframes scrolling {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
                transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
        -webkit-transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
                transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
    }
}
.marquee-content li {
    display: flex;
    justify-content: center;
    align-items: center;
    /* text-align: center; */
    flex-shrink: 0;
    width: calc(80vw / 5);
    width: var(--marquee-element-width);
    max-height: 100%;
    font-size: calc(12vh * 3 / 4);
    font-size: calc(var(--marquee-height) * 3 / 4); /* 5rem; */
    white-space: nowrap;
}

.marquee-content li img {
    width: 100%;
    /* height: 100%; */
    border: 2px solid #eee;
}

@media screen and (max-width: 800px) {
    
    :root {
        --marquee-width: 80vw;
        --marquee-height: 16vh;
        --marquee-elements-displayed: 3;
    }
    .marquee:before,
    .marquee:after {
        width: 5rem;
    }
    .carousel-box{
        margin: 200px auto 0px;
        flex-direction: column;  
    }
    .carousel-title{
        margin-bottom:20px; 
    } 
    .marquee-content li{
        font-size: 55px;
    }
}

